import 'regenerator-runtime/runtime.js';
import { PreorderApi } from '~common/types';
import * as utils from '~utils/common/common';

export const IS_MOCK = false;

async function start() {
    let apiHost =
        (process.env.NODE_ENV === 'production' &&
            window.__preorder_env__?.REACT_APP_API_HOST) ||
        process.env.REACT_APP_API_HOST!;
    if (apiHost.endsWith('/')) {
        apiHost = apiHost.slice(0, -1);
    }

    const baseURL = apiHost + '/api';

    const preorderApi: PreorderApi = {
        url: baseURL,
    };
    const isProductPage = utils.isProductPage();
    
    if (isProductPage) {
        const productController = await import('./product-details/product');
        productController.init(preorderApi);
    }
    if (isProductPage || utils.isCartPage()) {
        const cartController = await import('./cart/cart');
        cartController.init(preorderApi, isProductPage);
    }
}

start();
