export type LogFn = (...str: any[]) => void;

export const log: LogFn = (str: string, ...args: any[]) => {
    if (process.env.NODE_ENV === 'production') {
        return '';
    }

    let result = `APP-BOSS::${str ?? ''}`;
    console.groupCollapsed(result, ...args);
    console.trace();
    console.groupEnd();

    // result = args.reduce((acc, arg) => acc + JSON.stringify(arg), result);
    // return result;
};

export function logWithPrefix(prefix?: string) {
    return log.bind(null, prefix ?? '');
}

const stylePropertyValid = (property: string, value: unknown) => {
    const key = parseInt(property);
    return (
        isNaN(key) &&
        typeof value !== 'undefined' &&
        typeof value !== 'object' &&
        typeof value !== 'function' &&
        typeof value === 'string' &&
        value.length > 0 &&
        (typeof value === 'number' ? value != parseInt(value) : true)
    );
};

export function applyStyle(style: CSSStyleDeclaration, element: HTMLElement) {
    for (const property in style) {
        if (stylePropertyValid(property, style[property])) {
            element.style[property] = style[property];
        }
    }
    // Array.from(style).forEach(key => {
    //     if (typeof key === 'number') {
    //         return;
    //     }
    //     element.style.setProperty(key, style[key as string])
    // });
}

export const addStyleElement = (style: string) => {
    document.head.insertAdjacentHTML('beforeend', `<style>${style}</style>`);
};

