export const LocationApi = {
    // extend: function(t, e) {
    //     function o() {}
    //     o.prototype = e.prototype,
    //     t.prototype = new o,
    //     (t.prototype.constructor = t).baseConstructor = e,
    //     t.superClass = e.prototype
    // }

    locationSearch: function () {
        return window.location.search;
    },

    locationHash: function () {
        return window.location.hash;
    },

    replaceState: function (url: string) {
        window.history.replaceState({}, document.title, url);
    },

    urlParam: function (param: string) {
        var e = RegExp('[?&]' + param + '=([^&#]*)').exec(
            this.locationSearch()
        );
        return e && decodeURIComponent(e[1].replace(/\+/g, ' '));
    },

    newState: function (param: string, value: string) {
        return (
            (this.urlParam(param)
                ? this.locationSearch().replace(
                    RegExp('(' + param + '=)[^&#]+'),
                    '$1' + value
                )
                : '' === this.locationSearch()
                    ? '?' + param + '=' + value
                    : this.locationSearch() + '&' + param + '=' + value) +
            this.locationHash()
        );
    },

    setParam: function (param: string, value: string) {
        this.replaceState(this.newState(param, value));
    },
};

export const isProductDetailsPage = () => {
    const path = window.location.pathname;
    const isProductPage = path.startsWith('/products/');

    return isProductPage;
};

export const isProductDetailsFromFrontpagePage = () => {
    const path = window.location.pathname;
    const isProductPage = path.startsWith('/collections/frontpage/products/');

    return isProductPage;
};

export const isProductDetailsFromCollectionPage = () => {
    const path = window.location.pathname;
    const isProductPage = /^\/collections\/.+\/products\//g.test(path);

    return isProductPage;
};

export const isPreviewProductsPage = () => {
    const path = window.location.pathname;
    const host = window.location.host;
    const isPreviewProductsPage =
        host.includes('shopifypreview.com') &&
        path.startsWith('/products_preview');

    return isPreviewProductsPage;
};

export const isProductPage = () =>
    isProductDetailsPage() ||
    isProductDetailsFromFrontpagePage() ||
    isProductDetailsFromCollectionPage() ||
    isPreviewProductsPage();



export const isCartBasePage = () => {
    const path = window.location.pathname;
    const isProductPage = path.startsWith('/cart');

    return isProductPage;
};

export const isCartPage = () =>
    isCartBasePage();
    
