
import pRetry from 'p-retry';
import { logWithPrefix } from './common';
import { PreorderError } from './errors';

type FetchParams = Parameters<typeof fetch>;

export function fetchJsonBase<T>(...args: FetchParams) {
    return fetch(...args).then(async response => ({
        status: response.status,
        json: await response.json() as T
    }))
};


export async function fetchJson<T = unknown>(fetchParams: { input: RequestInfo, init?: RequestInit } | string, retryOptions?: pRetry.Options) {
    let input: RequestInfo = '';
    let init: RequestInit | undefined;
    const logFetch = logWithPrefix('fetchJson:: ');

    if (typeof fetchParams === 'string') {
        input = fetchParams;
    }
    else {
        input = fetchParams.input;
        init = fetchParams.init;
    }

    const responseFn = async () => {
        const { json, status } = await fetchJsonBase<T>(input, init);
        logFetch('params:: ', input, init, 'response:: ', 'status:', status, ' json: ', json);

        if ([410, 404].includes(status)) {
            throw new pRetry.AbortError((json as any)?.message);
        }

        if (status !== 200) {
            throw new PreorderError(`retries failed on ${fetchParams}`);
        }

        return json;
    };

    return pRetry<T | null>(responseFn, retryOptions);
};

export function loadScript(url: string) {
    return new Promise(function (resolve, reject) {
        var script = document.createElement("script");
        script.onload = resolve;
        script.onerror = reject;
        script.src = url;
        document.getElementsByTagName("head")[0].appendChild(script);
    });
}